<template>
  <main>
    <section class="team" id="team">
      <h1 class="team__title">Bizning jamoamiz</h1>
      <div class="team__content">
        <div class="team__block animation" data-speed="2000">
          <img src="@/assets/Image/personale1.jpg" alt="">
          <h3 class="team__block-name">Maxmudov Fahriddin</h3>
          <p class="team__block-text">Oliy toifali manual terapiya tabibi</p>
        </div>
        <div class="team__block animation" data-speed="2000">
          <img src="@/assets/Image/personale2.jpg" alt="">
          <h3 class="team__block-name">Muxtorov Muxammad</h3>
          <p class="team__block-text">Oliy toifali Chiropractor tabibi</p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
export default {
  mounted() {
    const parent = document.querySelector(`#team`);
    let observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry, index) => {
          if (entry.isIntersecting) {
            const el = parent.querySelectorAll(".animation");
            el.forEach((i) => {
              let sp = i.getAttribute("data-speed");
              i.classList.add("animate__animated");
              i.style.transition = sp + "ms";
            });
          }
        });
      },
      { threshold: 0 }
    );

    observer.observe(parent);
  },
}
</script>

